import { AboutComponent } from './about/about.component';
import { SopComponent } from './sop/sop.component';
import { RegulationComponent } from './regulation/regulation.component';
import { SubmissionManualComponent } from './submission-manual/submission-manual.component';
import { HomeComponent } from './home/home.component';
import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

const routes: Routes = [
  {
    path: '',
    redirectTo: '/home',
    pathMatch: 'full'
  },
  {
    path: 'home',
    component: HomeComponent
  },
  {
    path: 'submission-manaul',
    component: SubmissionManualComponent
  },
  {
    path: 'regulation',
    component: RegulationComponent
  },
  {
    path: 'sop',
    component: SopComponent
  },
  {
    path: 'about',
    component: AboutComponent
  }
];

@NgModule({
  imports: [RouterModule.forRoot(routes,
    {
      useHash: false,
      anchorScrolling: 'enabled',
      onSameUrlNavigation: 'reload',
      scrollPositionRestoration: 'enabled',
      scrollOffset: [0, 150]
    })],
  exports: [RouterModule]
})
export class AppRoutingModule { }
